<template>
    <div class="pt-5 pl-5 pr-5">
      <v-card color="#55B4B0" rounded="xl">
        <v-card-title class="justify-center text-h4 pt-5 pb-5 font-weight-black">LIST OF REGISTERED MEDICINES</v-card-title>
      </v-card>

      <v-container fluid>
        <v-row dense>
          <v-col v-for="post in posts" :key="post.id" :cols="4">
            <v-card color="55B4B0" class="mx-auto">
              <v-card-title>{{ post.title }}</v-card-title>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </div>
</template>

<script>
    import MedicinesApiService from "@/services/medicines-api.service";

    export default {
      data: () => ({
        posts: [],
      }),

      created() {
        this.retrievePosts();
      },

      methods: {
        retrievePosts(){
          console.log("ABCD")
          MedicinesApiService.getAllMedicines()
          .then(response => {
            console.log(response)
            this.posts = response.data;
            this.displayPosts = response.data.map(this.getDisplayPost);
            console.log(this.displayPosts)
          })
          .catch((e) => {
            console.log(e);
          });
        },

        getDisplayPost(post) {
          return {
            id: post.id,
            title: post.title,
            body: post.body
          };
        }
      }

    }

</script>

